@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;300;400;600&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Inter', sans-serif;
  line-height: 1.15;
}

html {
  font-size: 10px;
}

button {
  background: none;
  color: inherit;
  font: inherit; /* 1 */
  border: none;
  margin: 0; /* 2 */
}

button:hover {
  cursor: pointer;
}

.container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  width: 930px;
  height: 630px;
  box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  padding: 40px;

  background-color: #393f49;

  color: white;
}

header {
  border-bottom: 1px solid #8d9196;
}

.header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
}
.date {
  font-size: 1.6rem;
  font-weight: 600;
}

nav {
  display: flex;
}

.link {
  margin-left: 5rem;
  font-size: 1.6rem;
  color: #8d9196;
  text-decoration: none;
}
.link:hover {
  cursor: pointer;
}

.active {
  color: white;
}

.activeTasks {
  font-size: 1.2rem;
  color: #61dbfb;
  margin-bottom: 2rem;
}

.form input {
  width: 360px;
  padding: 1rem;
  font-size: 1.4rem;
  border: none;
  border-radius: 3px;
  margin-right: 1rem;
}
.form button {
  background-color: #61dbfb;
  box-sizing: content-box;
  padding: 1rem 1.5rem;
  font-size: 1.4rem;
  border-radius: 3px;
}
.taskForm {
  margin-bottom: 4rem;
}

.errorMsg {
  margin-top: 1rem;
  font-size: 1rem;
  color: red;
}

ul {
  list-style: none;
  height: 393px;
  overflow-y: scroll;
}

li:first-child {
  border-top: none;
}
li {
  position: relative;
  display: flex;
  border-top: 1px solid #8d9196;
  height: 70px;
}
.tickBtn {
  margin-right: 2rem;
}
.description {
  flex-grow: 1;
  margin: auto 0;
  font-size: 1.6rem;
  color: white;
}
.removeBtn {
  position: absolute;
  top: 50%;
  right: 1rem;
  transform: translateY(-50%);
}
.blue {
  color: #61dbfb;
}

